/* src/components/EmployeeList.css */
.employee-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .employee-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 300px;
    padding: 20px;
    background-color: #fff;
    text-align: center;
  }
  
  .employee-header {
    margin-bottom: 20px;
  }
  
  .employee-photo img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .employee-details p {
    margin: 10px 0;
  }
  
  .employee-footer {
    margin-top: 20px;
  }
  
  .employee-footer button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
    margin: 0 10px;
  }
  
  .employee-footer button:disabled {
    background-color: #ddd;
    cursor: not-allowed;
  }

