  /* src/components/EmployeeList.css */

.employee-container {
  background-color: black; /* Fondo negro */
  color: white; /* Texto blanco para contraste */
  min-height: 100vh; /* Asegúrate de que el contenedor ocupe toda la altura de la pantalla */
}

.header {
  padding: 20px;
  border-bottom: 1px solid #444; /* Agregar un borde inferior para separación visual */
}

.employee-card {
  background-color: #222; /* Fondo de la tarjeta de empleado */
  border-radius: 10px;
  padding: 20px;
  margin: 20px auto;
  max-width: 800px;
  color: white; /* Texto blanco dentro de la tarjeta */
}

.employee-header h1 {
  text-align: center;
}

.employee-photo img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
}

.employee-details p {
  margin: 10px 0;
}

.employee-qr {
  text-align: center;
  margin-top: 20px;
}

.employee-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

button {
  background-color: #555; /* Color de fondo de los botones */
  color: white; /* Texto blanco en los botones */
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

button:disabled {
  background-color: #333; /* Color para los botones deshabilitados */
}

button:hover {
  background-color: #777; /* Cambio de color al pasar el ratón sobre los botones */
}
